// CG
export function getCGConfigByCharacter(characterName: string): CharacterCGConfig | null {
  console.log('[CG] CG:', characterName);
  console.log(
    '[CG] :',
    CG_CONFIGS.map(c => c.characterName),
  );

  // 
  const config = CG_CONFIGS.find(cfg => {
    const match = characterName.includes(cfg.characterName) || cfg.characterName.includes(characterName);
    console.log(`[CG] : "${characterName}" vs "${cfg.characterName}" = ${match}`);
    return match;
  });

  if (config) {
    console.log('[CG] :', config.characterName);
  } else {
    console.warn('[CG] ');
  }

  return config || null;
}

// CG
export function selectCGEvent(
  characterName: string,
  playerGender: '' | '',
  isVictory: boolean,
): { event: CGEvent; imageUrl: string; description: string } | null {
  console.log('[CG] ========== CG ==========');
  console.log('[CG]  - :', characterName, ':', playerGender, ':', isVictory);

  const config = getCGConfigByCharacter(characterName);
  if (!config) {
    console.warn('[CG] null');
    return null;
  }

  // 
  const genderKey = playerGender === '' ? 'male' : 'female';
  const resultKey = isVictory ? 'victory' : 'defeat';
  console.log('[CG]  - key:', genderKey, 'key:', resultKey);

  const events = config[genderKey][resultKey];
  console.log('[CG] :', events);
  console.log('[CG] :', events?.length || 0);

  if (!events || events.length === 0) {
    console.warn('[CG] null');
    return null;
  }

  // 
  const rareEvents = events.filter(e => e.probability !== undefined);
  const normalEvents = events.filter(e => e.probability === undefined);
  console.log('[CG] :', rareEvents.length, ':', normalEvents.length);

  let selectedEvent: CGEvent | null = null;

  // 
  for (const rareEvent of rareEvents) {
    const roll = Math.random();
    console.log(`[CG] : "${rareEvent.name}" :${rareEvent.probability} :${roll}`);
    if (roll < rareEvent.probability!) {
      selectedEvent = rareEvent;
      console.log('[CG]  :', rareEvent.name);
      break;
    }
  }

  // 
  if (!selectedEvent && normalEvents.length > 0) {
    const randomIndex = Math.floor(Math.random() * normalEvents.length);
    selectedEvent = normalEvents[randomIndex];
    console.log('[CG]  (', randomIndex, '):', selectedEvent.name);
  }

  if (!selectedEvent) {
    console.warn('[CG] null');
    return null;
  }

  // 
  const randomImageIndex = Math.floor(Math.random() * selectedEvent.images.length);
  const randomImage = selectedEvent.images[randomImageIndex];
  console.log('[CG] :', selectedEvent.images);
  console.log('[CG]  (', randomImageIndex, '):', randomImage);

  // URL
  const genderFolder = playerGender === '' ? 'u' : 'u';
  const resultFolder = isVictory ? '' : '';
  const imageUrl = `https://raw.githubusercontent.com/vincentrong2005/Fatria/main///cg/${config.characterName}/${genderFolder}/${resultFolder}/${randomImage}`;

  console.log('[CG] URL:', imageUrl);
  console.log('[CG] :', selectedEvent.description);
  console.log('[CG] ========== CG ==========');

  return {
    event: selectedEvent,
    imageUrl,
    description: selectedEvent.description,
  };
}
